import React, {useState} from 'react'
import '../Styles/Calculator.css'
import TextField from '@material-ui/core/TextField';

const Calculator = () => {
  const [count, setCount] = useState(0);
  const [ selectedValue, setValue ] = useState()
  const onSetValue = (e) => {
    setValue(e.target.selectedValue)
  }
  return (
    <div className="calculator">
      <div className="calculator__wrapper">
        <h1 className="calculator__title">Calculator</h1>
        <h2 className="calculator__subtitle">Nam libero tempore, cum soluta nobis est eligendi optio</h2>
        <div className="calculator__container">
        <div className="calculator__container calculator__container--column">
        <div className="calculator__container ">
          <div className="calculator__container calculator__container--padding">
            <select defaultValue={'DEFAULT'} value={selectedValue} onChange={(e)=> onSetValue(e)} className="caltulator__select">
              <option value="DEFAULT" disabled>Colors</option>
              <option value="">red</option>
              <option value="">green</option>
              <option value="">blue</option>
              <option value="">orange</option>
            </select>
            <select value={selectedValue} defaultValue={'DEFAULT'} onChange={(e)=> onSetValue(e)} className="caltulator__select" >
              <option value="DEFAULT" disabled >Size</option>
              <option value="">150%</option>
              <option value="">200%</option>
              <option value="">250%</option>
            </select>
          </div>
          <div className="calculator__container calculator__container--padding">
          <div className="calculator__couter">
            <button className="couter__minus" onClick={() => setCount(count - 1)}>-</button>
            {count}
            <button className="couter__plus" onClick={() => setCount(count + 1)}>+</button>
          </div>
            <TextField
              id="date"
              type="date"
              defaultValue="2017-05-24"
              className="calculator__date"
            />
          </div>
        </div> 
        <div className="calculator__container calculator__container--padding">
          <select value={selectedValue}  defaultValue={'DEFAULT'} onChange={(e)=> onSetValue(e)} className="caltulator__select caltulator__select--second" >
            <option value="DEFAULT" disabled>Sity</option>
            <option value="">Kyiv</option>
            <option value="">Odessa</option>
            <option value="">Rivne</option>
            <option value="">Lviv</option>
          </select>
          <select value={selectedValue}  defaultValue={'DEFAULT'} onChange={(e)=> onSetValue(e)} className="caltulator__select caltulator__select--second" >
            <option value="DEFAULT" disabled>Delivery</option>
            <option value="">New Post</option>
            <option value="">Ukrposhta</option>
            <option value="">Pickup</option>
          </select>
        </div>
        </div>
        <div className="calculator__container calculator__container--max-width calculator__container--column">
          <div className="calculator__sum">$ 50.25</div>
          <button className="calculator__btn">Proced</button>
        </div>
        </div>
      </div>
    </div>
  )
}

export default Calculator
